<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>个人信息</span>
    </div>
    <div class="text item">
      <div class="left">
        <el-form ref="form" :model="form" label-width="80px" style="max-width:70%" :rules="rules">
          <el-form-item label="用户名" prop="name">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="手机号码" prop="mobile">
            <el-input v-model="form.mobile" disabled></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="form.email"></el-input>
          </el-form-item>
          <el-form-item label="简介" prop="intro">
            <el-input v-model="form.intro" type="textarea"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="open">保存修改</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="right">
        <el-upload
        class="avatar-uploader"
          action=""
          list-type="picture-card"
          :show-file-list="false"
          :http-request="httpRequest"
        >
          <!-- <img width="100%" v-if="form.photo" :src="form.photo" alt />
          <i v-else class="el-icon-plus"></i> -->
            <img v-if="form.photo" :src="form.photo" class="avatar" style="width:150px">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </div>
    </div>
  </el-card>
</template>

<script>
import Pass from '@/components/pass.js'
export default {
  data () {
    return {
      dialogVisible: '',
      form: {
        name: '',
        mobile: '',
        email: '',
        intro: '',
        photo: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入活动名称', trigger: 'blur' },
          { min: 5, max: 7, message: '长度在 5 到 7 个字符', trigger: 'blur' }
        ],
        email: [{ required: true, message: '请填写邮箱信息', trigger: 'blur' }],
        intro: [{ message: '请填写内容信息', trigger: 'blur' }]
      }
    }
  },
  methods: {
    httpRequest (resource) {
      let fd = new FormData()
      // fd.photo = resource.file
      fd.append('photo', resource.file)
      this.$http.patch('/user/photo', fd)
        .then(result => {
          this.form.photo = result.data.data.photo
          this.$message.success('头像更新成功')
          Pass.$emit('Photo', result.data.data.photo)
        })
        .catch(err => {
          this.$message.error('错了哦，这是一条错误消息' + err)
        })
    },
    open (form) {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.$http.patch('/user/profile', {
            name: this.form.name,
            intro: this.form.intro,
            email: this.form.email
          })
            .then(result => {
              this.$message.success('保存信息成功')
              //  本地存储
              // let user = window.sessionStorage.getItem('userinfo')
              // user = JSON.parse(user)
              // user.name = result.data.data.name
              // window.sessionStorage.setItem('userinfo', JSON.stringify(user))
              Pass.$emit('pass', result)
            })
            .catch(err => {
              console.log(err)
              this.$message.error('保存信息失败')
            })
        } else {
          return false
        }
      })
    },
    // 获取首屏数据
    getData () {
      this.$http.get('/user/profile').then(result => {
        this.form = result.data.data
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="less" scoped>
.left {
  width: 70%;
  float: left;
}
.right {
  width: 30%;
  float: right;
}
 .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    display: block;
  }
</style>
